
const defaultState = {
    majorMenuUrl: 'all',
    currentMajorMenu: ''
}

export default (state = defaultState, action) => {
    switch(action.type) {
        case "0":
            return {
                majorMenuUrl: 'all',
                currentMajorMenu: ''
            }
        case "1":
            return {
                majorMenuUrl: 'kuaiji',
                currentMajorMenu: ''
            }
        case "2":
            return {
                majorMenuUrl: 'jisuanji',
                currentMajorMenu: ''
            }
        case "3":
            return {
                majorMenuUrl: 'qichedianzi',
                currentMajorMenu: ''
            }
        case "4":
            return {
                majorMenuUrl: 'qicheyingxiao',
                currentMajorMenu: ''
            }
        case "5":
            return {
                majorMenuUrl: 'qicheyunyong',
                currentMajorMenu: ''
            }
        case "6":
            return {
                majorMenuUrl: 'shukong',
                currentMajorMenu: ''
            }
        default:
            return state
    }
}