
const defaultState = {
	carouseltitle: ''
}

export default (state = defaultState, action) => {
    switch(action.type) {
        case -1: 
            return {
                carouseltitle: ''
            }
        case 0:
            return {
                carouseltitle: ''
            }
        case 1:
            return {
                carouseltitle: ''
            }
        case 2:
            return {
                carouseltitle: ''
            }
        case 3:
            return {
                carouseltitle: ''
            }
        default:
            return state
    }
}